
import { defineComponent } from 'vue';
import imagepath from '@/assets/images';

export default defineComponent({
  name: 'entertainCard',
  props: {
    listinfo:Object,
    routename:String
  },
  data(){
      return{
          lists: [{
            id: "",
            name: "",
            explanation: "",
            photopath: ""
          }]
      }
  },
  mounted: function(){
    this.getLists()
  },
  methods:{
    getLists: function(){

      if(this.listinfo !== undefined){
        this.lists = this.listinfo.lists;
      }

      this.lists.forEach(element => {
        if(this.listinfo !== undefined){
          element.photopath = imagepath[this.listinfo.category][element.photopath];
        }
      });
    },
    detailClick: function(idnum:String){
      this.$router.push({name: this.routename, params: {'id':String(idnum)}});
    }
  },
  
});
